import returns from '../common/returns.vue'
export default {
	data() {
		return {
			title:'确认订单',
			data:[],
			payment:'213',
			id:'',
			payment_id:'',
			require_data:'',
			coupon_list:'',
			show:false,
			recording:'未选择',
			v_price:''
		}
	},
	components:{
		returns
	},
	methods:{
		choice(index){
			for (let s of this.payment) {
				s.choice = false
			}
			this.payment[index].choice = true
			this.payment_id = this.payment[index].id
			this.payment_name = this.payment[index].payment
		},
		discount_choice(index){
			for (let s of this.coupon_list) {
				s.choice = false
			}
			this.coupon_list[index].choice = true
			console.log(this.coupon_list)
		},
		finish(){
			this.show = false
			this.coupon_list.forEach((item,index) => {
				if(item.choice == true){
					if(item.cid){
						this.require_data.coupon_id = this.coupon_list[index].cid
						this.recording = '￥'+this.coupon_list[index].discount_value+'元'
						this.v_price = this.v_price - this.coupon_list[index].discount_value
					}else{
						this.require_data.coupon_id = 0
						this.recording = '不使用'
					}
				}
			})
			console.log(this.require_data)
		},
		Index(){ //下单时间确定
			this.service.entire(this,'post',this.APIconfig.api_root.com_page.v_orderDetail,this.require_data,function(self,res){//订单信息
				self.data = res.data.video
				self.v_price = res.data.video.v_price
				console.log(res.data.coupon)
				if(res.data.coupon != ''){
					let not_coupon = {
						not_coupon:'不使用'	
					}
					let coupon_data = res.data.coupon
					coupon_data.push(not_coupon)
					for (let s of coupon_data) {
						s.choice = false
					}
					self.coupon_list = coupon_data
					console.log(self.coupon_list)
				}
				let data = res.data.pay_type
				for (let s of data) {
					s.choice = false
				}
				self.payment = data
			})
			
		},
		payments(){  //提交
			let that = this
			if(!this.payment_id){
				uni.showToast({
					icon:'none',
					title:'请选择支付方式'
				})
				return
			}
			uni.showModal({
			    title: '提示',
			    content: '是否确定支付？',
				
			    success: function (res) {
			        if (res.confirm) {
			            // console.log('用户点击确定');
						let data = {
							pay_type:that.payment_name
						}
						let data_list = Object.assign(data,that.require_data)
						console.log(data_list)
						// return
						that.service.entire(that,'post',that.APIconfig.api_root.com_page.v_saveOrder,data_list,function(self,res){
							console.log(res)
							if(res.code == 0){
								
								
								// self.service.entire(self,'post',self.APIconfig.api_root.com_page.order_pay,{
								// 	user_id: that.$store.state.user.id,
								// 	id:res.data.order.id
								// },function(self,ref){
								// 	console.log(ref)
								// 	self.service.order(ref,self,'../subuser/s_order?status=-1','pages/subuser/s_order?status=-1')
								// })
							}else{
								uni.showToast({
									icon:'none',
									title:res.msg
								})
							}
							
						})
			        } else if (res.cancel) {
			            // console.log('用户点击取消');
						return
			        }
			    }
			});
			
		}
	},
	onLoad(e) {
		console.log(e)
		let require_data = {
			userid: this.$store.state.user.id,
			video_id:e.id,
			section_id:0,
			coupon_id:0
		}
		this.require_data = require_data
	},
	onShow() {
		this.Index()
	}
}
